import React from "react";
import { Outlet } from "react-router-dom";
import Navbar from "./Navbar";
import CartContextProvider from "../context/CartContext";

const pages = () => {
  return (
    <>
      <CartContextProvider>
        <Navbar />
        <div className="container-fluid text-center p-5">
          <Outlet></Outlet>
        </div>
      </CartContextProvider>
    </>
  );
};

export default pages;
