import React, { useContext } from "react";
import { CartContext } from "../context/CartContext";

const ProductCard = ({ product }) => {
  let { getProductQuantity, addOneToCart, removeOneFromCart, deleteFromCart } =
    useContext(CartContext);

  const quantity = getProductQuantity(product.id);
  return (
    <>
      <div className="col-xl-3 col-lg-4 col-md-6">
        <div className="card shadow ">
          <img
            src={product.imgUrl}
            className="card-img-top"
            alt="..."
            style={{ height: "13rem", objectFit: "cover" }}
          />
          <div className="card-body">
            <h5 className="card-title d-flex justify-content-between align-items-center mb-3">
              <span> {product.name}</span>
              <span>${product.price.toFixed(2)}</span>
            </h5>
            {quantity > 0 ? (
              <div className="">
                <button
                  className="btn btn-success me-2"
                  onClick={() => addOneToCart(product.id)}
                >
                  +
                </button>
                <span className=" me-2">in Cart {quantity}</span>
                <button
                  className="btn btn-danger"
                  onClick={() => removeOneFromCart(product.id)}
                >
                  -
                </button>
                <div className="pt-4">
                  <button
                    className="btn btn-danger px-4 py-2"
                    onClick={() => deleteFromCart(product.id)}
                  >
                    Remove From Cart
                  </button>
                </div>
              </div>
            ) : (
              <button
                className="btn  btn-success"
                onClick={() => addOneToCart(product.id)}
              >
                Add To Cart
              </button>
            )}
          </div>
        </div>
      </div>
    </>
  );
};

export default ProductCard;
