import img1 from "./imgs/banana.jpg";
import img2 from "./imgs/book.jpg";
import img3 from "./imgs/car.jpg";
import img4 from "./imgs/computer.jpg";

const productList = [
  {
    id: 1,
    name: "Book",
    price: 10.99,
    imgUrl: img2,
  },
  {
    id: 2,
    name: "Computer",
    price: 1199,
    imgUrl: img4,
  },
  {
    id: 3,
    name: "Banana",
    price: 1.05,
    imgUrl: img1,
  },
  {
    id: 4,
    name: "Car",
    price: 14000,
    imgUrl: img3,
  },
];

function getProductData(id) {
  let productData = productList.find((product) => product.id === id);

  if (productData == undefined) {
    console.log("Product not found" + id);
    return undefined;
  }

  return productData;
}

export { productList, getProductData };
