import React, { useContext } from "react";
import { CartContext } from "../context/CartContext";
import AddShoppingCartIcon from "@mui/icons-material/AddShoppingCart";
import CartProduct from "./CartProduct";
import { Link } from "react-router-dom";

const Navbar = () => {
  const { cartProducts, getTotalCost, setCartProducts } =
    useContext(CartContext);
  const productCount = cartProducts.reduce(
    (sum, product) => sum + product.quantity,
    0
  );
  return (
    <>
      <nav className="navbar navbar-expand-lg bg-dark navbar-dark px-5">
        <div className="container-fluid">
          <Link to={"/"} className="navbar-brand">
            Shooping Card
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul
              className="navbar-nav  w-100  d-flex justify-content-end align-items-center"
              style={{ width: "40px", height: "40px" }}
            >
              <li
                className="nav-item border rounded-circle p-2  position-relative"
                type="button"
                data-bs-toggle="modal"
                data-bs-target="#exampleModal"
              >
                <AddShoppingCartIcon className="text-white fs-3 " />
                <span className="productCount text-danger position-absolute top-0  fw-bolder fs-4">
                  {productCount}
                </span>
              </li>
            </ul>
          </div>
        </div>
      </nav>
      {/* Modal */}
      <div
        className="modal fade rounded-3"
        id="exampleModal"
        tabIndex="-1"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog modal-dialog-centered">
          <div className="modal-content">
            <div className="modal-header">
              <h1 className="modal-title fs-5" id="exampleModalLabel">
                {productCount} Items in Your Cart
              </h1>
              <button
                type="button"
                className="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <div className="modal-body">
              {cartProducts.map((produnt, index) => (
                <CartProduct produnt={produnt} key={index} />
              ))}
              <div className="d-flex justify-content-between fw-bold py-2 px-3  rounded-3 ">
                <span>Total Cost:</span>
                <span>${getTotalCost().toFixed(2)}</span>
              </div>
            </div>

            <div
              className="modal-footer d-flex justify-content-center"
              data-bs-dismiss="modal"
              aria-label="Close"
            >
              <Link
                to={"/complete"}
                className="btn btn-outline-success"
                onClick={() => setCartProducts([])}
              >
                Go To Checkout
              </Link>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Navbar;
