import React from "react";
import { getProductData } from "../productsStore";

const CartProduct = ({ produnt }) => {
  let productData = getProductData(produnt.id);
  return (
    <>
      <div className="mb-3 border border-top-0 border-end-0 border-start">
        <h6 className="fw-bold">{productData.name}</h6>
        <span className="d-flex justify-content-between fw-bold">
          <span>x{produnt.quantity}</span>
          <span>${produnt.quantity * productData.price}</span>
        </span>
      </div>
    </>
  );
};

export default CartProduct;
